@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
button,
input {
  font-family: "Josefin Sans", sans-serif;
}

body {
  max-height: 100vh;
  max-width: 100vw;
  overflow: hidden;
}

.Toastify__toast-theme--dark {
  background-color: #00000076 !important;
}
.container1 {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    grid-gap: 3rem;
    gap: 3rem;
    background-color: #131324;
    height: 100vh;
    width: 100vw;
}

.loader {
    height: 30rem;
    max-inline-size: 100%;
}

.title-container h1 {
    color: white;
}

.avatars {
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
}

.avatar {
    border: 0.4rem solid transparent;
    padding: 0.4rem;
    border-radius: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.5s ease-in-out;
}

img {
    height: 6rem;
    transition: 0.5s ease-in-out;
}

.selected-avatar {
    border: 0.4rem solid #4e0eff;
}

.submit-btn {
    background-color: #997af0;
    color: white;
    padding: 1rem 2rem;
    border: none;
    font-weight: bold;
    cursor: pointer;
    border-radius: 0.4rem;
    font-size: 1rem;
    text-transform: uppercase;
}

.submit-btn:hover {
    background-color: #4e0eff;
}
.chat-input-container {
    display: grid;
    align-items: center;
    grid-template-columns: 5% 95%;
    background-color: #080420;
    padding: 0 2rem;

    @media screen and (min-width: 720px) and (max-width: 1080px) {
        padding: 0 1rem;
        grid-gap: 1rem;
        gap: 1rem;
    }

}

.button-container {
    display: flex;
    align-items: center;
    color: white;
    grid-gap: 1rem;
    gap: 1rem;

}

.emoji {
    position: relative;
}

.emoji svg {
    font-size: 1.5rem;
    color: #ffff00c8;
    cursor: pointer;
}



.input-container {
    width: 100%;
    border-radius: 2rem;
    display: flex;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
    background-color: #ffffff34;
}

.input-container input {
    width: 90%;
    height: 60%;
    background-color: transparent;
    color: white;
    border: none;
    padding-left: 1rem;
    font-size: 1.2rem;
}

.input-container input::selection {
    background-color: #9a86f3;
}

.input-container input:focus {
    outline: none;

}

button {
    padding: 0.3rem 2rem;
    border-radius: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #9a86f3;
    border: none;

    @media screen and (min-width: 720px) and (max-width: 1080px) {
        padding: 0.3rem 1rem;

        svg {
            font-size: 1rem;
        }
    }
}

button svg {
    font-size: 2rem;
    color: white;
}

.EmojiPickerReact {
    background-color: #080420;
    box-shadow: 0 5px 10px #9a86f3;
    border-color: #9a86f3;
}

aside.EmojiPickerReact.epr-main {
    position: absolute;
    top: -420px;

}
.logout {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.5rem;
    border-radius: 0.5rem;
    border: none;
    cursor: pointer;
    background-color: #9a86f3;
  }
  
  .logout svg {
    font-size: 1.3rem;
    color: #ebe7ff;
  }
  
.chat-container {
    display: grid;
    grid-template-rows: 10% 80% 10%;
    grid-gap: 0.1rem;
    gap: 0.1rem;
    overflow: hidden;

    @media screen and (min-width: 720px) and (max-width: 1080px) {
        grid-template-rows: 15% 70% 15%;
    }
}

.chat-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 2rem;
}

.user-details {
    display: flex;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
}

.user-avatar-img img {
    height: 3rem;
}

.username h3 {
    color: white;
}

.chat-messages {
    padding: 1rem 2rem;
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    overflow: auto;
}

.chat-messages::-webkit-scrollbar {
    width: 0.2rem;
}

::-webkit-scrollbar-thumb {
    background-color: #ffffff39;
    width: 0.1rem;
    border-radius: 1rem;
}

.message {
    display: flex;
    align-items: center;
}

.message .content {
    max-width: 40%;
    overflow-wrap: break-word;
    padding: 1rem;
    font-size: 1.1rem;
    border-radius: 1rem;
    color: #d1d1d1;

    @media screen and (min-width: 720px) and (max-width: 1080px) {
        max-width: 70%;
    }
}

.sended {
    justify-content: flex-end;
}

.sended .content {
    background-color: #4f04ff21;
}

.received {
    justify-content: flex-start;
}

.received .content {
    background-color: #9900ff20;
}


.contact-container {
  display: grid;
  grid-template-rows: 10% 75% 15%;
  overflow: hidden;
  background-color: #080420;
}

.brand {
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: center;
}


h3 {
  color: white;
}

.contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: auto;
  grid-gap: 0.8rem;
  gap: 0.8rem;
}


::-webkit-scrollbar {
  width: 0.2rem;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(155, 155, 155, 0.5);
  border-radius: 20px;
  border: transparent;
}


.contact {
  background-color: #ffffff34;
  min-height: 5rem;
  cursor: pointer;
  width: 90%;
  border-radius: 0.2rem;
  padding: 0.2rem;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
  transition: 0.5s ease-in-out;
}

.logo {
  height: 3rem;
}
.avatar img {
  height: 4rem;
}

.selected {
  background-color: #9a86f3;
}

.current-user {
  background-color: #0d0d30;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
}

.user-avatar img {
  height: 4rem;
  max-inline-size: 100%;
  margin-top: 1rem;
}

.user-avatar{
  position: relative;
  height: 100px;
  width: 100px;
}

.user-icon,
.edit {
  width: 100%;
  height: 100%;
  position: absolute;
}

.user-avatar:hover .edit {
    display: block;
}

.edit{
  margin: 4rem 0 0 4.1rem ;
  color: white;
  display: none;
}

h2 {
  color: white;
}

@media screen and (min-width: 720px) and (max-width: 1080px) {
  .username h2 {
    font-size: 1rem;
  }
}

span {
  color: #4e0eff
}

.status {
  color: white;
  padding-top: 0.8rem;
  font-size: 0.8rem;
}

.status svg {
  padding-top: 0.2rem;
  color: greenyellow;
}
.welcome-text {
    color: white;
  }
  
  .welcome-container {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    flex-direction: column;
  }
  
  .robot {
    height: 20rem;
  }
  
.Chat {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: center;
    background-color: #131324;
}

.container {
    height: 85vh;
    width: 85vw;
    background-color: #00000076;
    display: grid;
    grid-template-columns: 25% 75%;

    @media screen and (min-width: 720px) and (max-width: 1080px) {
        grid-template-columns: 35% 65%;
    }
}
.FormContainer {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column; 
    grid-gap: 1rem; 
    gap: 1rem;
    justify-content: center;
    align-items: center;
    background-color: #131324;
}

.brand {
    display: flex;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: center;
} 
 
img {
    height: 5rem;
}


.title {
    color: white;
    text-transform: uppercase;
}

.auth-form{
    display: flex;
    flex-direction: column;
    padding: 3rem 5rem;
    background-color: #00000076;
    border-radius: 2rem;
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.auth-form input {
    border-radius: 2rem;
    padding: 1rem;
    background-color: transparent;
    border: 0.1rem solid #4e0eff;
    border-radius: 0.4rem;
    color: white;
    width: 100%;
    font-size: 1rem;
    
}
.auth-form input:focus{
    border: 0.1rem solid #997af0;
    outline: none;
}


.create {
    background-color: #4e0eff;
    color: white;
    padding: 1rem 2rem;
    border: none;
    font-weight: bold;
    cursor: pointer;
    border-radius: 0.4rem;
    font-size: 1rem;
    text-transform: uppercase;
    
}

.create:hover {
    background-color: #4e0eff;
}

.nav {
    color: white;
    text-transform: uppercase;
}

span a {
    color: #4e0eff;
    text-decoration: none;
    font-weight: bold;
}  


